.topBar{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    line-height: 40px;
    border-bottom: mintcream 1px solid;
    -ms-flex-align: center;
        align-items: center;
}

.hamburger{
    -ms-flex: 1 1 17px;
        flex: 1 1 17px;
}

.hamburger img{
    height: 17px;
    cursor: pointer;
    -webkit-transition: 0.5s ease;
    -o-transition: 0.5s ease;
    transition: 0.5s ease;

}

.search{
    margin: 10px;
}

.search img{
    height: 17px;
}

.space{
    -ms-flex: 20 20 1px;
        flex: 20 20 1px;
}

.addProject{
    background-color: #5584ff;
    border-radius: 20px;
    color: white;
    border: none;
    padding: 7px 17px;
}

.messages{
    -ms-flex: 1 1 23px;
        flex: 1 1 23px;
}

.messages img{
    padding-top: 15px;
    width: 23px;
}

.notifications{
    -ms-flex: 1 1 20px;
        flex: 1 1 20px;
}

.notifications img{
    padding-top: 15px; 
    width: 20px;
}

.profileAvatar{
    white-space: nowrap;
    -ms-flex: 1 1 20px;
        flex: 1 1 20px;
}

.photo{
    padding-top: 15px;
    width: 33px;
}

.polygon{
    padding-left: 5px;
    padding-bottom: 10px;    
    width: 10px;
}

.topBar div{
    margin: 10px;
}

.menu-hided {
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
    -webkit-transition: 0.5s ease;
    -o-transition: 0.5s ease;
    transition: 0.5s ease;
}


